      <v-list-item-title>Закрытый тикет</v-list-item-title>
    </v-list-item>
  </template>
    <v-card-text>
    <v-text-field autofocus @keyup.enter.prevent="open" v-model="id" label="ID"/>
  </v-card-text>
  <v-card-actions>
    <v-btn @click="open" :loading="loading" color="primary">Перейти</v-btn>
  </v-card-actions>
</MyDialog>
</template>

<script>
import MyDialog from '@/render/components/Common/MyDialog';

export default {
  components: { MyDialog },
  data: () => ({
    id: '',
    loading: false
  }),

  methods: {
    async open () {
      this.tabMgr.open('Ticket', { ticketId: this.id });
    }
  }
}
</script>
